
@keyframes playing {
0% {
    -webkit-transform: scale(0.5);
    transform: scale(0.5);
}
25% {
    -webkit-transform: scale(1);
    transform: scale(1);
}
50% {
    -webkit-transform: scale(0.5);
    transform: scale(0.5);
}
75% {
    -webkit-transform: scale(1);
    transform: scale(1);
}
100% {
    -webkit-transform: scale(0.5);
    transform: scale(0.5);
}
}
@-webkit-keyframes playing {
0% {
    -webkit-transform: scale(0.5);
    transform: scale(0.5);
}
25% {
    -webkit-transform: scale(1);
    transform: scale(1);
}
50% {
    -webkit-transform: scale(0.5);
    transform: scale(0.5);
}
75% {
    -webkit-transform: scale(1);
    transform: scale(1);
}
100% {
    -webkit-transform: scale(0.5);
    transform: scale(0.5);
}
}
.audio-player {
  cursor: pointer;
  text-align: left;
  font-size: 14px;
  padding: 10px;
  height: 20px;
  line-height: 20px;
}
.audio-player .icon-wave-right {
    vertical-align: middle;
    margin-right: 0.7rem;
    display: inline-block;
}
.audio-player .icon-wave-right::before {
      color: inherit;
}
.audio-player audio {
    display: none;
}
.audio-player.playing .icon-wave-right {
    -webkit-animation-name: playing;
    animation-name: playing;
    -webkit-animation-duration: 3s;
    animation-duration: 3s;
    -webkit-animation-iteration-count: infinite;
    animation-iteration-count: infinite;
}



