
.upload-btn {
  position: relative;
  overflow: hidden;
  width: 30px;
}
.upload-btn > form > input {
    position: absolute;
    top: 0;
    right: 0;
    margin: 0;
    opacity: 1;
    border: 1px solid red;
    -webkit-transform: translate(-300px, 0) scale(4);
    -webkit-transform: translate(-300px, 0) scale(4);
    transform: translate(-300px, 0) scale(4);
    font-size: 23px;
    direction: ltr;
    cursor: pointer;
}



